/* RedItemTitle */
.RedItemTitle {
    display: flex;
    width: 100%;
    padding: 5px 8px;
    background: linear-gradient(180deg, #3f3f3f, rgba(0, 0, 0, 0));
    border-top: 1px solid var(--panel-title-border-top-color);
    border-radius: 6px 10px 0 0;
    color: #efb26a;
    font-size: 11px;
}